<template>
  <div class='reward-outter'>
    <div class="rewards-list">
      <ul v-if="rewardList.length>0">
        <li v-for="x in rewardList">
          <div class="reward-info">
            <div class="reward-name">{{x.prizeName}}</div>
            <div class="reward-time">{{x.createDatetime}}</div>
          </div>
          <div class="reward-address"
               v-if="x.prizeId != 5">
            <div class="status"
                 @click="editAddress">{{x.prizeStatus}}</div>
          </div>
        </li>
      </ul>
      <div v-if="isShowtip"
           class="rewards-none">
        <p>您还没有抽过奖哦！</p>
        <p>赶快去参加活动吧～</p>
      </div>
    </div>
  </div>
</template>
<script>
import { getRewardList } from '@/api/rokiMarchActivity'
export default {
  name: 'myRewards',
  data() {
    return {
      rewardList: [],
      isShowtip: false,
    }
  },
  created() {
    const { userId } = this.$route.params
    getRewardList(userId).then((res) => {
      if (res.code == 200) {
        this.rewardList = res.data
        this.isShowtip = res.data && res.data.length ? false : true
      }
    })
  },
  mounted() {
    document.querySelector('body').setAttribute('style', 'background:#f5f5f8')
  },
  methods: {
    editAddress() {
      const { userId } = this.$route.params
      const { terminal } = this.$route.query
      this.$router.push(`/address/${userId}?terminal=${terminal}`)
    },
  },
  //   beforeDestroy() {
  //     document.querySelector('body').removeAttribute('style')
  //   },
  beforeRouteLeave(to, form, next) {
    document.querySelector('body').removeAttribute('style')
    next()
  },
}
</script>

<style lang='scss' scoped>
.reward-outter {
  min-height: calc(100% + var(--safe-area-inset-bottom));
}
.rewards-list {
  width: 100%;

  font-family: PingFang SC;
  ul {
    display: flex;
    flex-direction: column;
    li {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 36px 30px 32px;
      margin-bottom: 10px;
      background: #fff;
      .reward-name {
        text-align: left;
        margin-bottom: 24px;
        font-size: 30px;
        font-weight: 500;
        color: #333333;
      }
      .reward-time {
        font-size: 24px;
        color: #666666;
      }
      &:first-child {
        margin-top: 19px;
      }
      .reward-address {
        .status {
          font-size: 28px;

          font-weight: 400;
          color: #2871f8;
        }
      }
    }
  }
}
.rewards-none {
  padding-top: 508px;
  p {
    margin: 0;
    font-size: 36px;
    color: #333333;
  }
}
</style>



