import { rewardRegularReq } from '@/utils/request';
export function getRewardList(userId) {
    return rewardRegularReq({
        url: `/cook-manage-admin/api/ops/activity/lottery/myPrize?userId=${userId}`,
        method: 'get'
    })
}
export function saveAddress(data) {
    return rewardRegularReq({
        url: `/cook-manage-admin/api/ops/activity/lottery/saveAddress`,
        method: 'post',
        data
    })
}
export function getUserInfo(userId) {
    return rewardRegularReq({
        url: `/cook-manage-admin/api/ops/activity/lottery/userInit?userId=${userId}`,
        method: 'get',
    })
}


